import React from 'react';
import { ImageTimeAnimation } from './animation';

export default function BasicRisu({ name, x, y, center, scale }) {
  let style = {};
  if (center) {
    style = {display: 'block', margin: '0 auto'};
  }
  else {
    style = {display: 'inline-block', position: 'relative', marginLeft: x || 0, marginTop: y || 0};
  }
  if (scale) {
    style['transform'] = `scale(${scale}, ${scale})`;
  }
  return (
    <ImageTimeAnimation
      alt="matsurisu"
      style={style}
      frameDur={5}
      srcs={[`/matsurisu/static/img/${name}1.png`, `/matsurisu/static/img/${name}2.png`]}
    />
  );
}
