import React, { useContext, useState, useRef, useLayoutEffect } from 'react';
import { ContainerContext } from './scene';

export default function Fade({ children, start, end, fadeDur, offset }) {
  offset = offset || 0;
  fadeDur = fadeDur || 300;
  const [height, setHeight] = useState(0);
  const ref = useRef(null)
  const containerDims = useContext(ContainerContext);

  const t = -containerDims.y;
  const show = (t > start - fadeDur) && (t < end + fadeDur);
  const opacity = 1 - (Math.max(Math.min((start - t) / fadeDur, 1), 0) + Math.max(Math.min((t - end) / fadeDur, 1), 0));
  const top = containerDims.height / 2 - height / 2 + offset;
  
  useLayoutEffect(() => {
    if (!ref.current) return;
    setHeight(ref.current.getBoundingClientRect().height);
  }, [ref, setHeight, containerDims]);

  return (
    (show)
    ? <div ref={ref} className="fade" style={{top, opacity}}>
        {children}
      </div>
    : null
  );
}
